:root {
  --ui-color-root: hsl(0, 0%, 100%);
  --ui-color-background: hsl(0, 0%, 95%);
  --ui-color-background-inverted: hsl(240, 8%, 17%);
  --ui-color-text: hsl(240, 8%, 17%);
  --ui-color-theme: hsl(201, 100%, 40%);
  --ui-color-theme-knockout: hsl(0, 0%, 95%);
  --ui-color-input-well: hsla(0, 0%, 0%, 0.05);
  --ui-color-input-well-focus: hsla(0, 0%, 0%, 0.075);
}

@media (prefers-color-scheme: dark) {
  :root {
    --ui-color-root: hsl(0, 0%, 0%);
    --ui-color-background: hsl(240, 8%, 17%);
    --ui-color-background-inverted: hsl(0, 0%, 95%);
    --ui-color-text: hsl(0, 0%, 95%);
    --ui-color-theme: hsl(201, 100%, 40%);
    --ui-color-theme-knockout: hsl(0, 0%, 95%);
    --ui-color-input-well: hsla(0, 0%, 100%, 0.05);
    --ui-color-input-well-focus: hsla(0, 0%, 100%, 0.075);
  }
}
