.button {
  background-color: var(--ui-color-theme);
  border: 0;
  border-radius: 0;
  color: var(--ui-color-theme-knockout);
  cursor: pointer;
  font-size: 1rem;
  font-variation-settings: 'opsz' 28;
  font-weight: 600;
  padding: 0.75rem 1rem;
  transition: filter 200ms ease;

  &:focus,
  &:hover {
    filter: brightness(110%);
  }

  &:active {
    filter: brightness(90%);
  }
}
