.root {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: minmax(0, 1fr);
}

.rootLabel,
.rootInput {
  transition: background-color 200ms ease, filter 200ms ease, color 200ms ease;
}

.rootLabel {
  margin-left: 1rem;
}

.rootIndicator,
.rootInput {
  align-self: end;
  grid-area: 1 / 1;
}

.rootIndicator {
  background-color: var(--ui-color-theme);
  height: 2px;
  transform-origin: left;
}

.rootInput {
  background-color: var(--ui-color-input-well);
  border: 0;
  border-radius: 0;
  color: inherit;
  font: inherit;
  padding: 1em;
}

.rootInput:hover,
.rootInput:focus {
  background-color: var(--ui-color-input-well-focus);
  outline: 0;
}

.rootInput:focus + .rootLabel {
  filter: opacity(0.5);
}
