.app__header {
  grid-column: 1 / -1;
  padding: clamp(1rem, 10vw, 3rem) 0;
  width: min(28em, 80vw);

  &-logo {
    margin: 0 0 clamp(1rem, 10vw, 3rem);
  }

  &-title {
    font-size: clamp(2rem, 5vw, 3rem);
    font-weight: 800;
    letter-spacing: -0.025em;
    margin: 0;
  }

  &-subtitle {
    opacity: 0.75;
  }
}
